import { Component, OnInit } from '@angular/core';
import {Http, Response} from '@angular/http';
import { Router,ActivatedRoute} from '@angular/router';

@Component({
  selector: 'app-search',
  templateUrl: './search.component.html',
  styleUrls: ['./search.component.scss']
})
export class SearchComponent implements OnInit {
  // input value [coming key]
  countryname:string;
  allCountries;
  // searched keys
  appCountName;
  appCountNativeName;
  appCountCapital;
  appCountcallingCodes;
  appCountTopDomain
  // we will equal this var to countryname
  searchName;
  // search result
  finalobject;
  // country data
  country_flag;
  country_name;
  country_nativeName;
  country_population;
  country_region;
  country_subregion;
  country_capital;
  country_topLevel;
  country_currencies;
  country_languages;
  country_borders;
  country_call;
  // this constructor receive data from API and Parameter
  constructor(private http:Http, private router:Router,private active:ActivatedRoute) {
    this.active.params.subscribe(Params => {
     this.countryname = Params.name;
     this.http.get('../../assets/allcountries.json').subscribe((data:Response) =>{
       this.allCountries = data.json();
     })
    })
    this.doSearch2(); // default function
  }
  ngOnInit() {}
  // Go to HomePage using navigate
  goback(){
    this.router.navigate(['/']);
  }
  // Function that return your Request Country
  doSearch(){
    if(this.countryname == ''){
      return false;
    } else{

      this.router.navigate(['search',this.countryname]);
      this.searchName = this.countryname.toLocaleLowerCase();

      for(let i = 0; i < this.allCountries.length; i++){

        this.finalobject = this.allCountries[i];
        this.appCountName = this.finalobject.name.toLocaleLowerCase();
        this.appCountNativeName = this.finalobject.nativeName.toLocaleLowerCase();
        this.appCountCapital = this.finalobject.capital.toLocaleLowerCase();
        this.appCountcallingCodes = this.finalobject.callingCodes[0].toLocaleLowerCase();
        this.appCountTopDomain = this.finalobject.topLevelDomain[0].toLocaleLowerCase();

        if(this.searchName == this.appCountName || 
          this.searchName == this.appCountNativeName || 
          this.searchName == this.appCountCapital || 
          this.searchName == this.appCountcallingCodes || 
          this.searchName == this.appCountTopDomain ){
          
          this.country_currencies = this.finalobject.currencies[0].name;
          this.country_languages = this.finalobject.languages;
          this.country_borders = this.finalobject.borders;
          if(this.country_borders == 0){
                this.country_borders[0] = "Borders Not Found";
          }
          this.country_flag = this.finalobject.flag;
          this.country_name = this.finalobject.name;
          this.country_nativeName = this.finalobject.nativeName;
          this.country_population = this.finalobject.population;
          this.country_region = this.finalobject.region;
          this.country_subregion = this.finalobject.subregion;
          this.country_capital = this.finalobject.capital;
          this.country_topLevel = this.finalobject.topLevelDomain;
          this.country_call = this.finalobject.callingCodes[0];
        }
      }
    }
  }
  // Default Function Return Default Country in Search page
  doSearch2(){
      this.country_flag="https://restcountries.eu/data/egy.svg";
      this.country_name = 'Egypt';
      this.country_nativeName = 'ÙØµØ±';
      this.country_population = '91290000';
      this.country_region = 'Africa';
      this.country_subregion = 'Northern Africa';
      this.country_capital = 'Cairo';
      this.country_topLevel = ['.Eg'];
      this.country_currencies = 'Egyptian Pound';
      this.country_languages = [{name:'Arabic'}];
      this.country_borders=['ISR','LBY','SDN']
      this.country_call = "20";
  }
}