import { Component, OnInit } from '@angular/core';
import {ActivatedRoute} from '@angular/router';
import{ Http, Response } from '@angular/http';
import{ Router } from '@angular/router';

@Component({
  selector: 'app-details',
  templateUrl: './details.component.html',
  styleUrls: ['./details.component.scss']
})
export class DetailsComponent implements OnInit {

  id;
  country;
  selected_country;
  country_flag;
  country_name;
  country_nativeName;
  country_population;
  country_region;
  country_subregion;
  country_capital;
  country_topLevel;
  country_currencies;
  country_languages;
  country_borders;
  country_call;

  constructor(private roue:ActivatedRoute, 
    private http:Http,
    private router:Router) { 
    this.roue.paramMap.subscribe(Params=> this.id = Params.get('id'));
    this.http.get('../../assets/allcountries.json').subscribe((data:Response) =>{
      this.country = data.json();
      //console.log(this.id)

      this.selected_country = this.country[this.id];

      this.country_currencies = this.selected_country.currencies;
      this.country_languages = this.selected_country.languages;
      this.country_borders = this.selected_country.borders;
      if(this.country_borders == 0){
        this.country_borders[0] = "Borders Not Found";
      }
      this.country_flag = this.selected_country.flag;
      this.country_name = this.selected_country.name;

      this.country_nativeName = this.selected_country.nativeName;
      this.country_population = this.selected_country.population;
      this.country_region = this.selected_country.region;
      this.country_subregion = this.selected_country.subregion;
      this.country_capital = this.selected_country.capital;
      this.country_topLevel = this.selected_country.topLevelDomain;
      this.country_call = this.selected_country.callingCodes;
    })
  }
  // Go to HomePage using navigate
  goback(){
    this.router.navigate(['/']);
  }
  ngOnInit() {}
}
