import { Component, OnInit } from '@angular/core';
import {Http, Response} from '@angular/http';
import {Router, ActivatedRoute} from '@angular/router';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss']
})
export class HomeComponent implements OnInit {
  countryname = 'egypt'; // default param
  // contain comming data from API
  countries;
  filtercountries;
  freq; // object contain all countries
  region='Filter By Region'; // region from select form
  appReg; // app region
  myArr=[]; // array contain freq
  constructor(private http:Http, private router:Router, private active:ActivatedRoute) {
      this.http.get('../../assets/allcountries.json').subscribe((data:Response) =>{
        this.countries = data.json();
      })
  }
  ngOnInit() {}
  // redirect to search Page
  goSearch(){
    this.router.navigate(['search',this.countryname]);
  }
  // Filter Depending on Region
  filterNow(){
    // console.log(this.region);
    this.myArr=[];
    for(let i=0; i < this.countries.length; i++){
      this.appReg = this.countries[i].region;
      this.freq = this.countries[i]; // object
      if(this.region == this.appReg){
        this.myArr.push(this.freq);
        //console.log(i);
      }
    }
    this.filtercountries = this.myArr;
    //console.log(this.filtercountries);
  }
}
