import { Component } from '@angular/core';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent {
  title = 'app';
  constructor(){}

  bodyBack='light';
  backInverse='dark';

  changeTheme(){
    if(this.bodyBack == 'light'){
      this.bodyBack = 'dark';
      this.backInverse = 'light';
    } else{
      this.bodyBack = 'light'
      this.backInverse = 'dark';
    }
  }
  
}
