import { Component, OnInit } from '@angular/core';
import {ActivatedRoute} from '@angular/router';
import{ Http, Response } from '@angular/http';
import{ Router } from '@angular/router';
import { log } from 'util';

@Component({
  selector: 'app-filterdetails',
  templateUrl: './filterdetails.component.html',
  styleUrls: ['./filterdetails.component.scss']
})
export class FilterdetailsComponent implements OnInit {

  name:string; // parameter come from homeComponent
  appRegion:string; // catch Request name for country region requested from API
  country; // all Countries before Filter
  reqCount; // the Requested Country
  // Request country Data
  c_name;
  c_flag;
  c_native;
  c_capital;
  c_region;
  c_sub_region;
  c_pop;
  c_top;
  c_call;
  country_currencies;
  country_languages;
  country_borders;
  // Constructor get api data
  constructor(private roue:ActivatedRoute, 
    private http:Http,
    private router:Router) { 
    this.roue.paramMap.subscribe(Params=> this.name = Params.get('name'));
    this.http.get('../../assets/allcountries.json').subscribe((data:Response) =>{
      this.country = data.json();
      for(let i = 0; i < this.country.length; i++){
        this.appRegion = this.country[i].name;
        if(this.appRegion == this.name){
          this.reqCount = this.country[i];
          this.c_name = this.reqCount.name;
          this.c_flag = this.reqCount.flag;
          this.c_native = this.reqCount.nativeName;
          this.c_capital = this.reqCount.capital;
          this.c_region = this.reqCount.region;
          this.c_sub_region = this.reqCount.subregion;
          this.c_pop = this.reqCount.population;
          this.c_top = this.reqCount.topLevelDomain;
          this.c_call = this.reqCount.callingCodes;
          this.country_currencies = this.reqCount.currencies;
          this.country_languages = this.reqCount.languages;
          this.country_borders = this.reqCount.borders;
          if(this.country_borders == 0){
             this.country_borders[0] = "Borders Not Found";
          }
          //console.log(this.c_name+" : "+this.c_flag+" : "+this.c_native+" : "+this.c_capital+" : "+this.c_region+" : "+this.c_sub_region+" : "+this.c_pop+" : "+this.c_top+" : "+this.c_call+" : "+this.country_currencies+" : "+this.country_languages+" : "+this.country_borders);
        }
      }
    })
  }

  // Go to HomePage using navigate
  goback(){
    this.router.navigate(['/']);
  }
  ngOnInit() {}
}
